.my-modal.open .modal-overlay, .my-modal.open .modal-window {
  opacity: 1;
  z-index: 200;
}

.modal-window, .modal-overlay {
  opacity: 0;
  z-index: -1;
}

.modal-overlay {
  background: #0000004d;
  position: fixed;
  inset: 0;
}

.modal-window {
  width: 600px;
  height: 200px;
  background: #fff;
  border-radius: 5px;
  margin: 100px auto;
  position: relative;
}

.modal-header {
  justify-content: space-between;
  align-items: flex-start;
  padding: 5px 10px;
  display: flex;
}

.modal-close {
  cursor: pointer;
  font-size: 2rem;
  position: absolute;
  top: 0;
  right: 2%;
}

.modal-body {
  padding: 5px;
}

